<template>
  <div>
    <div class="d-flex text-center text-white mb-4" :first_item="first_item">
      <div
        v-for="(button, index) in group_buttons"
        :key="index"
        :data-index="index"
        :class="{
          'w-100': v_100
        }"
      >
        <span
          class=" px-3 py-2 border click-pointer d-block"
          :class="{
            'bg-secondary': current_onglet !== button.id,
            'bg-primary': current_onglet === button.id
          }"
          @click="select_curent_onglet(button.id, index)"
          v-html="button.value"
        ></span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "MenusButtons",
  props: {
    group_buttons: Array,
    // pour que les bouton occupe tout l'espace disponible.
    v_100: { type: Boolean, default: false }
  },
  data() {
    return {
      current_onglet: "",
      /**
       * Cette variable doit etre supprimer,
       * Pour le moment on force avec.
       */
      select_first: true,
      stop_after: false
    };
  },
  computed: {
    first_item: {
      get() {
        if (this.group_buttons.length && this.select_first) {
          this.select_first_line();
          return 0;
        }
        return "";
      }
    }
  },
  methods: {
    select_curent_onglet(value, index) {
      this.current_onglet = value;
      //console.log("select_curent_onglet : ", value, index);
      this.$emit("ev_menu_button", {
        status: true,
        index: index,
        articles: this.group_buttons[index].articles
          ? this.group_buttons[index].articles
          : [],
        id: this.group_buttons[index].id
      });
    },
    select_first_line() {
      //console.log("select_first_line automatique"); //
      var fiestitem = this.group_buttons[0];
      var value = fiestitem.id;
      var index = 0;
      this.select_curent_onglet(value, index);
      if (this.stop_after) {
        this.select_first = false;
      }
    }
  }
};
</script>
