<template>
  <div class="setting-action">
    <span class="button " @click="edit_ligne('edit')" v-if="edit_button">
      <i class="fas fa-edit click-pointer text-warning mr-4"></i
    ></span>
    <span class="button" @click="delete_data('delete')">
      <i class="far fa-trash-alt click-pointer text-danger"></i
    ></span>
  </div>
</template>
<script>
export default {
  name: "TemplateTableauActionExterne",
  props: {
    ligne: {
      type: [String, Boolean, Object, Array]
    },
    /**
     * Identifiant de la ligne(du tableau).
     */
    index: {
      type: [String, Number]
    }
  },
  data() {
    return {
      edit_button: true
    };
  },
  methods: {
    edit_ligne(action) {
      this.send_action(action);
    },
    delete_data(action) {
      this.send_action(action);
    },
    send_action(action) {
      this.$emit("ev_template_tableau_action", {
        ligne: this.ligne,
        index: this.index,
        action: action
      });
    },
    displayEditBouton(value = false) {
      this.edit_button = value;
    }
  }
};
</script>

<style lang="scss" scoped></style>

